import axios from 'axios'
// ___________________________________________ LOGIN _______________________________________
export function logUser(email, password) {
    return axios.post('http://localhost:3000/api/auth/login', {
        email: email,
        password: password
    })
}

// ___________________________________________ SIGNUP _______________________________________
export function signupUser(firstname, lastname, tel, email, password, adress, zip, city) {
    return axios.post('http://localhost:3000/api/auth/signup', {
        firstname: firstname,
        lastname: lastname,
        tel: tel,
        email: email,
        password: password,
        adress: adress,
        zip: zip,
        city: city

    })
}

// ___________________________________________ UPDATE _______________________________________

export function updateUserById(userId, firstname, lastname, tel, email, password, adress, zip, city, token) {
    return axios.put(`http://localhost:8080/api/users/${userId}`, {
        firstname: firstname,
        lastname: lastname,
        tel: tel,
        email: email,
        password: password,
        adress: adress,
        zip: zip,
        city: city



    }, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })
}

// ___________________________________________ DELETE _______________________________________

export function deleteUserById(userId, token) {
    return axios.delete(`http://localhost:8080/api/users/${userId}`, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })
}


// ___________________________________________ GET USER _______________________________________


export function getUserById(userId, token) {
    return axios.get(`http://localhost:8080/api/users/${userId}`, {
        headers: {
            'Authorization': `bearer ${token}`

        }
    })
}