<template>
  <div class="connexion">
      <form class="form" action="">
          <input v-model="email" class=" input" type="email" placeholder="✉️ mail">
          <input v-model="password" class=" input" type="password" placeholder="🔐 password">
          <p v-if="errorMsg">Problème d'authentification!</p>
          <button class="btn_white" @click="connect">Se connecter</button>
      </form>
  </div>
</template>

<script>
// @ is an alias to /src
import {logUser} from '../api/user.api'

export default {
  name: 'Connexion',
  data(){
    return{
        email:"",
        password:"",
        errorMsg:false

    }
  },
  methods:{
      connect(e){
        e.preventDefault();
         logUser(this.email,this.password)
         .then(response=>{
                    console.log(response)
        //___________ saugarde les éléments de navigation 
                    this.$store.state.userId=response.data.userId
                    this.$store.state.admin=response.data.admin
                    this.$store.state.token=response.data.token
                    this.$store.state.refreshToken=response.data.refreshToken
                    this.$store.state.connected=true
                    localStorage.setItem("token","response.data.token")
                    

        //___________ envois à la page utilisateur
                    this.$router.push({path:'/myaccount'})
                    console.log(this.$store.state.connected)
        //___________ active le refresh token
                    this.$store.dispatch("refreshMyToken")
                })
                .catch(error=>{
                    console.log(error);
                    this.errorMsg = true;
                })

      }
  }
}
</script>

